import Home from './homework2/Home';
import Doc from './homework2/Doc';
import Tuto from './homework2/Tuto';
import Blog from './homework2/Blog';
import Comm from './homework2/Comm';
import Login from './homework2/Login';
import React from 'react';

const routes = [
    {
        path:'/',
        exact:true,
        component:Home
    },

    {
        path:'/doc',
        component:Doc,
        pri:true,
        
        routes:[
            {
                path:'/doc/core',
                component:Core,
            },
            {
                path:'/doc/guide',
                component:Guide,
            },
            {
                path:'/doc/api',
                component:Api,
            },
            {
                path:'/doc/hooks',
                component:Hooks,
            },
        ]
    },

    {
        path:'/tuto',
        component:Tuto,
        pri:true
    },

    {
        path:'/blog',
        component:Blog
    },

    {
        path:'/comm',
        component:Comm
    },

    {
        path:'/login',
        component:Login
    }
]


function Core(){
    return (
        <h1>
            Core
        </h1>
    )
}

function Guide() {
    return (
        <h1>
            Guide
        </h1>
        
    )
}

function Api(){
    return (
        <h1>
            Api
        </h1>
    )
}

function Hooks(){
    return (
        <h1>
            Hooks
        </h1>
    )
}

export default routes;
