import React from 'react';
import {BrowserRouter as Router,Redirect,Route,NavLink,Switch, withRouter} from 'react-router-dom';
import Header from './Header';
import routes from '../router.config';
import RouteWithSubRoutes from './RouteWithSubRoutes.js';


export function PrivateRoute({ component:Com,...rest }) {
	return (
	  <Route
		{...rest}
		render={({ location }) =>
		   localStorage.getItem('userinfo')
		   ?<Com {...rest}/> 
		   : (
			<Redirect
			  to={{
				pathname: "/login",
				state: location 
			  }}
			/>
		  )
		}
	  />
	);
}
  
const App = ()=>{
	return <Router>
		<Header />
		<Switch>
			{
				routes.map((route, i) => {
					return <RouteWithSubRoutes key={i} {...route} />
				})
			}
		</Switch>
	</Router>
}

export default App;

 