import React from 'react';
import {PrivateRoute} from './App.js';
import {BrowserRouter as Router,Redirect,Route,NavLink,Switch, withRouter} from 'react-router-dom';

const RouteWithSubRoutes = (route) => {
	if(route.pri){
		return <PrivateRoute {...route}/> 
	}
	else{
		return (
			<Route
			  path={route.path}
			  render={props => (
				<route.component {...props} routes={route.routes} />
			  )}
			/>
		);
	}
}

export default RouteWithSubRoutes;
